body {
  margin: 0;
  padding: 0;
  max-width: 1400px;
  margin-left: auto;
  margin-right: auto;
  font-family: "Open Sans Regular", "Arial", sans-serif;
  font-size: 15px;
  line-height: 22px;
  font-weight: 400;
  color: #212121;
  letter-spacing: 0.05em;
  background-color: grey;
  position: relative; }

img {
  max-width: 100%;
  height: auto; }

* {
  box-sizing: border-box; }

.button {
  margin: 0;
  padding: 0;
  min-height: 55px;
  background-color: #00897b;
  border: 3px solid #00897b;
  font-family: "Open Sans Semibold", "Arial", sans-serif;
  font-size: 20px;
  font-weight: 600;
  color: #ffffff;
  text-transform: uppercase;
  text-align: center;
  vertical-align: middle;
  transition: 0.3s; }
  .button:hover {
    background-color: transparent;
    border: 3px solid #212121;
    color: #212121; }

.main-header {
  background-image: url("../../img/bg/header-bg.jpg");
  background-position: 0 0;
  background-repeat: no-repeat;
  position: relative; }

.menu-bar {
  background: #ffffff;
  padding: 20px 0; }

.header-content {
  min-height: 651px;
  text-align: center;
  padding-top: 182px; }

.header-content__title {
  margin: 0;
  padding: 0;
  margin-bottom: 26px;
  font-family: "Open Sans Semibold", "Arial", sans-serif;
  font-size: 60px;
  font-weight: 600;
  color: #ffffff; }

.header-content__text {
  margin: 0;
  padding: 0;
  margin-bottom: 108px;
  font-size: 22px;
  color: #ffffff; }

.header-content__btn {
  min-width: 220px;
  min-height: 70px; }
  .header-content__btn:first-child {
    margin-right: 25px; }
  .header-content__btn:hover {
    text-decoration: none;
    color: #ffffff;
    border: 3px solid #ffffff;
    background-color: transparent; }

.main-header__btn {
  position: absolute;
  bottom: 72px;
  left: 50%;
  transform: translateX(-100%);
  width: 23px;
  height: 15px;
  border: none;
  background-color: transparent;
  transition: 0.3s; }
  .main-header__btn:hover {
    transform: translateX(-100%) scale(1.6); }
  .main-header__btn::after {
    position: absolute;
    content: "";
    top: 6px;
    left: 0;
    transform: rotate(45deg);
    width: 14px;
    height: 4px;
    background-color: #ffffff; }
  .main-header__btn::before {
    position: absolute;
    content: "";
    top: 6px;
    right: 0;
    transform: rotate(-45deg);
    width: 14px;
    height: 4px;
    background-color: #ffffff; }

.logo__link:hover {
  text-decoration: none; }

.logo__img {
  display: block;
  width: 106px;
  height: 88px; }

.navigation__list {
  margin: 0;
  padding: 0;
  list-style: none; }

.navigation__link {
  font-size: 20px;
  color: #212121;
  padding-right: 30px;
  transition: 0.3s; }
  .navigation__link:hover {
    text-decoration: none;
    color: #00897b; }

.btn-search {
  border: none;
  background-color: transparent;
  transition: 0.3s; }
  .btn-search:hover svg {
    fill: #00897b; }

.section__title {
  margin: 0;
  padding: 0;
  margin-bottom: 36px;
  font-family: "Open Sans Bold", "Arial", sans-serif;
  font-size: 30px;
  line-height: 30px;
  font-weight: 700;
  color: #212121; }

.section__text {
  margin: 0;
  padding: 0; }

.about {
  background-color: #ffffff;
  padding-top: 125px;
  padding-bottom: 110px;
  text-align: center; }

.about__text {
  margin-bottom: 110px; }

.about__item-title {
  margin: 0;
  padding: 0;
  position: relative;
  padding-top: 100px;
  margin-bottom: 26px;
  font-family: "Open Sans Semibold", "Arial", sans-serif;
  font-size: 20px;
  font-weight: 600; }
  .about__item-title::before {
    position: absolute;
    content: "";
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    width: 62px;
    height: 62px;
    background-repeat: no-repeat;
    background-position: 0 0; }

.about__col:nth-child(1) .about__item-title::before {
  background-image: url("../../img/pic/about-item1.png"); }

.about__col:nth-child(2) .about__item-title::before {
  width: 70px;
  height: 70px;
  background-image: url("../../img/pic/about-item2.png"); }

.about__col:nth-child(3) .about__item-title::before {
  width: 63px;
  height: 63px;
  background-image: url("../../img/pic/about-item3.png"); }

.services {
  padding-top: 173px;
  padding-bottom: 120px;
  background-color: #e4fdf9;
  text-align: center; }

.services__text {
  margin-bottom: 100px; }

.services__item-icon {
  margin-bottom: 38px;
  width: 40px;
  height: 62px; }

.services__item-title {
  margin: 0;
  padding: 0;
  margin-bottom: 25px;
  font-family: "Open Sans Semibold", "Arial", sans-serif;
  font-size: 20px;
  font-weight: 600; }

.services__item-text {
  margin: 0;
  padding: 0;
  margin-bottom: 43px;
  font-family: "Open Sans Regular", "Arial", sans-serif;
  line-height: 25px; }

.services__item-link {
  text-decoration: none;
  display: inline-block;
  min-width: 150px;
  min-height: 55px;
  padding-top: 15px;
  background-color: transparent;
  border: 2px solid #212121;
  color: #212121; }
  .services__item-link:hover {
    text-decoration: none;
    color: #00897b; }

.services__card {
  margin-bottom: 30px;
  padding-left: 42px;
  padding-right: 42px;
  padding-top: 58px;
  padding-bottom: 50px;
  background-color: #ffffff;
  transition: 0.3s; }
  .services__card:hover {
    background-color: #00897b;
    box-shadow: 0px 10px 20px 0px rgba(0, 0, 0, 0.35); }
    .services__card:hover .services__item-title {
      color: #ffffff; }
    .services__card:hover .services__item-text {
      color: #ffffff; }
    .services__card:hover .services__item-link {
      color: #ffffff;
      border: 2px solid #ffffff; }
      .services__card:hover .services__item-link:hover {
        transform: scale(1.1);
        transition: 0.4s; }
    .services__card:hover .services__item-icon {
      fill: #ffffff; }

.portfolio {
  padding-top: 128px;
  padding-bottom: 135px;
  background-color: #ffffff;
  text-align: center; }

.portfolio__text {
  margin-bottom: 98px; }

.portfolio__item-title,
.portfolio__item-text {
  display: none; }

.portfolio__wrap {
  margin-bottom: 25px;
  position: relative; }
  .portfolio__wrap::before {
    position: absolute;
    content: "";
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: transparent; }
  .portfolio__wrap:hover::before {
    background-color: rgba(0, 0, 0, 0.6); }
  .portfolio__wrap:hover .portfolio__item-title {
    display: block;
    margin: 0;
    padding: 0;
    font-family: "Open Sans Semibold", "Arial", sans-serif;
    font-size: 20px;
    font-weight: 600;
