    top: 50%;
    left: 50%;
    transform: translateX(-50%) translateY(-50%); }
  .portfolio__wrap:hover .portfolio__item-text {
    display: block;
    margin: 0;
    padding: 0;
    font-family: "Open Sans Semibold", "Arial", sans-serif;
    font-size: 15px;
    font-weight: 600;
