  list-style: none;
  margin-bottom: 112px; }

.portfolio__tabs-item {
  margin-right: 20px; }

.portfolio__tabs-item:last-child {
  margin-right: 0; }

.portfolio__tabs-btn {
  display: block;
  padding-left: 25px;
  padding-right: 25px;
  background-color: transparent;
  border: 3px solid #00897b;
  color: #212121;
  padding-top: 12px; }
  .portfolio__tabs-btn:hover {
    background-color: #00897b;
    border: 3px solid #00897b;
    color: #ffffff; }

.portfolio__btn {
  min-width: 220px;
  min-height: 70px; }
